.preview-wrapper {
    max-width: 990px;
    display: flex;
    justify-content: flex-end;
    position: relative;
    z-index: 2;
    @include md {
        max-width: 900px; }
    @include sm {
        justify-content: center; } }

.preview {
    max-width: 600px;
    text-align: center;
    color: #ffffff;
    padding: 60px 0;
    margin-bottom: 116px;
    @include md {
        max-width: 450px; }
    @include sm {
        margin-bottom: 175px; }
    &__title {
        font-size: 72px;
        font-weight: 600;
        @include md {
            font-size: 40px; }
        @include sm {
            font-size: 36px; } }
    &__subtitle {
        font-size: 30px;
        font-weight: 600;
        margin-bottom: 25px;
        @include md {
            font-size: 18px; } } }

.btn_lg {
    border-radius: 32px;
    background-color: #1d354e;
    color: #fefeff;
    font-size: 18px;
    font-weight: 600;
    padding: 21px 71px;
    @include sm {
        font-size: 16px;
        padding: 15px 52px; } }

